@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap");
:root {
  --clr-primary-softBlue: #8bacda;
  --clr-primary-cyan: #00fff7;
  --clr-dark-blue-1: #0d192b;
  --clr-dark-blue-2: #14253d;
  --clr-dark-blue-3: #2f415b;
  --clr-white: rgb(255, 255, 255);
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  height: 100vh;
  margin: 6.2rem 2.4rem;
  background: var(--clr-dark-blue-1);
  display: flex;
  justify-content: center;
  align-items: center;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

ul {
  list-style-type: none;
}

a:link,
a:visited {
  text-decoration: none;
}

img {
  width: 100%;
  display: block;
}

:root {
  --font-outfit: "Outfit", sans-serif;
}

body {
  font-family: var(--font-outfit);
  color: var(--clr-primary-softBlue);
  font-size: 1.8rem;
  line-height: 1.44;
  font-weight: 300;
}

h1 {
  font-size: 2.2rem;
  font-weight: 600;
  line-height: 1.25;
  color: var(--clr-white);
  margin-bottom: 1.6rem;
}

.card {
  display: flex;
  flex-direction: column;
  row-gap: 2.4rem;
  background-color: var(--clr-dark-blue-2);
  padding: 2.5rem;
  border-radius: 1.5rem;
}
.card__img {
  height: 30.2rem;
  width: 30.2rem;
  background: var(--clr-primary-cyan);
  position: relative;
  overflow: hidden;
  border-radius: 8px;
}
.card__img-view {
  z-index: 1;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s linear;
}
.card__img::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transition: all 0.2s linear;
  background-image: url("/images/image-equilibrium.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  justify-self: center;
}
.card:hover .card__img-view {
  opacity: 1;
}
.card:hover .card__title {
  color: var(--clr-primary-cyan);
}
.card:hover .card__img::after {
  opacity: 0.5;
}
.card__text {
  margin-bottom: 24px;
  max-width: 29rem;
}
.card__eth {
  font-size: 1.6rem;
  display: flex;
  justify-content: space-between;
}
.card__eth-info {
  display: flex;
  column-gap: 0.7rem;
}
.card__eth--img {
  width: 11px;
  height: 20px;
}
.card__eth--amount {
  font-weight: bold;
  color: var(--clr-primary-cyan);
}
.card__days {
  display: flex;
  align-items: center;
  column-gap: 0.7rem;
}
.card__days--img {
  width: 13px;
  height: 13px;
}
.card__days--left {
  font-weight: 400;
}
.card__footer {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}
.card__footer-img {
  width: 33px;
  height: 33px;
}
.card__footer-avatar-name span {
  color: var(--clr-white);
}

@media screen and (max-width: 375px) {
  .card__img {
    height: 27.8rem;
    width: 27.8rem;
  }
}
.line {
  width: 100%;
  height: 1px;
  background-color: var(--clr-primary-softBlue);
}